<template>
  <div class="loading">
    <div class="cont">
      <div class="text">{{ text }}</div>
      <div class="ellipsis">{{ ellipsis }}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Loading',

  data () {
    return {
      text: 'loading',
      ellipsis: ''
    }
  },

  mounted () {
    this.loading()
    this.toHome()
  },

  methods: {
    loading () {
      setInterval(() => {
        if (this.ellipsis === '...') return this.ellipsis = ''
        this.ellipsis += '.'
      }, 400)
    },
    toHome () {
      setTimeout(() => {
        this.$router.push('/home')
      }, 3000)
    }
  }
}
</script>

<style lang="less" scoped>
.loading {
  z-index: 9999;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background-color: #fff;
  .cont {
    display: flex;
    width: 120px;
    height: 30px;
    font-size: 24px;
    letter-spacing: 2px;
  }
}
</style>
